        d="M405.651 331.551C407.134 331.551 408.612 330.952 409.692 329.773L424.951 313.112L452.245 325.099C454.593 326.13 457.343 325.395 458.862 323.328L475.606 300.55L477.013 310.355C477.405 313.086 479.748 315.057 482.429 315.056C482.688 315.056 482.95 315.038 483.213 315C486.208 314.57 488.288 311.794 487.858 308.799L484.532 285.607C484.102 282.612 481.325 280.534 478.331 280.962L455.139 284.289C452.145 284.718 450.065 287.494 450.495 290.489C450.924 293.484 453.7 295.564 456.695 295.134L467.085 293.644L452.634 313.303L425.765 301.502C423.617 300.559 421.107 301.088 419.522 302.818L401.613 322.372C399.569 324.603 399.721 328.069 401.952 330.112C403.004 331.075 404.33 331.551 405.651 331.551Z"
        fill="black"
      />
    </svg>
    <div class="card container col-lg-4 col-md-6 col-sm-10 col-xl-4 p-4">
      <h3 class="mb-4">Войдите в игру</h3>
      <form action @submit.prevent>
        <div class="form-group">
          <label for="name" class>Логин</label>
          <br />
          <input
            type="text"
            name="name"
            id="name"
            class="form-control form-control-lg"
            :class="{ 'is-invalid': $v.login.$error || serverError }"
            placeholder="Введите логин"
            v-model.trim="$v.login.$model"
            @click="clckForm()"
          />
          <div v-if="!$v.login.required" class="invalid-feedback">
            Обязательно введите имя!
          </div>
        </div>
        <div class="form-group">
          <label for="name" class>Пароль</label>
          <br />
          <input
            type="password"
            name="name"
            v-model.trim="$v.password.$model"
            class="form-control form-control-lg"
            :class="{ 'is-invalid': $v.password.$error || serverError }"
            placeholder="Введите пароль"
            @click="clckForm()"
          />
          <div v-if="!$v.login.required" class="invalid-feedback">
            Обязательно введите пароль!
          </div>
          <small class="form-text text-danger">
            {{ serverError }}
          </small>
        </div>
        <button
          v-if="authLoading"
          class="btn btn-lg btn-primary btn-block"
          disabled
        >
          <span
            class="spinner-border spinner-border-sm"
            role="status"
            aria-hidden="true"
          ></span>
          Загрузка...
        </button>
        <button
          v-else
          class="btn btn-lg btn-primary btn-block"
          @click="tryAuth()"
          :disabled="$v.$invalid"
        >
          Войти
        </button>
        <small class="form-text text-muted">
          Нет учётной записи?
          <router-link to="/registration">Зарегистрироваться!</router-link>
        </small>
      </form>
    </div>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import anime from "animejs/lib/anime.es.js";
import AnimBG from "./AnimBG";
export default {
  name: "Entrance",
  data() {
    return {
      formClicked: false,
      serverError: "",
      login: "",
      password: "",
      wasClicked: false,
      authLoading: false
    };
  },
  components: {
    AnimBG
  },
  validations: {
    login: {
      required
    },
    password: {
      required
    }
  },
  mounted() {
    this.name = this.$store.state.gamerName;
    if (this.wasClicked) this.formClicked = true;
    setInterval(() => {
      if (this.name !== "") {
        this.formClicked = true;
      }
    }, 100);
    // this.$store.commit("resetData");
    this.startTimeline();
  },
  computed: {
    gamerName() {
      return this.$store.state.gamerName;
    },
    errorClass() {
      let bool = this.name === "" && this.formClicked;
      return bool;
    },
    disabled() {
      if (!this.formClicked) {
        return true;
      } else return this.name === "" && this.formClicked;
    }
  },
  methods: {
    // Попытка авторизации
    tryAuth() {
      this.authLoading = true;
      const authData = {
        login: this.login,
        password: this.password
      };
      console.log("AUTH: ", authData);
      try {
        // Возращает promise
        // Если успешно, перенаправляет на путь "/"
        this.$store.dispatch("AUTH_REQUEST", authData).then(
          token => {
            // this.$socket.emit("authenticate", {
            //   token
            // });
            this.authLoading = false;
            this.$router.push("/choose");
          },
          err => {
            this.errorMessage = err;
            this.serverError = err.data.message;
            this.authLoading = false;
            console.log("ОШИБКА ВХОДА: ", err.data.message);
          }
        );
      } catch (error) {
        this.authLoading = false;
        this.errorMessage = error;
      }
    },
    clckForm() {
      this.formClicked = true;
      this.clickForm = true;
      this.serverError = "";
    },
    startTimeline() {
      anime
        .timeline({
          targets: "#path path",
          strokeDashoffset: [anime.setDashoffset, 0],
          easing: "easeInOutSine",
          duration: 1100,
          delay: function(el, i) {
            return i * 100;
          }
        })

        .add({
          targets: "#path path",
          delay: 2700,
          duration: 400,
          easing: "easeInOutSine"
        })
        .add({
          fill: "#000"
        })
        .add({
          targets: "#my-div .el",
          fill: "#000",
          direction: "alternate",
          scale: [
            {
              value: 0.4,
              easing: "easeOutSine",
              duration: 500
            },
            {
              value: 1,
              easing: "easeInOutQuad",
              duration: 1200
            }
          ],
          translateX: anime.stagger([10, 0], {
            grid: [11, 6],
            from: "center",
            axis: "x"
          }),
          translateY: anime.stagger([10, 0], {
            grid: [11, 6],
            from: "center",
            axis: "y"
          }),
          rotateZ: anime.stagger([0, 90, 0], {
            grid: [11, 6],
            from: "center",
            axis: "x"
          }),
          delay: anime.stagger(200, {
            grid: [11, 6],
            from: "center",
            easing: "easeOutQuad"
          })
        });
      anime({
        targets: ".el path",
        delay: 2900,
        duration: 3000,
        fill: "#7e7e7e"
      });
      anime({
        targets: "#my-div .el",
        direction: "alternate",
        scale: [
          {
            value: 0.2,
            easing: "easeOutSine",
            duration: 500
          },
          {
            value: 1,
            easing: "easeInOutQuad",
            duration: 1200
          }
        ],
        translateX: anime.stagger([10, 0], {
          grid: [11, 6],
          from: "center",
          axis: "x"
        }),
        translateY: anime.stagger([10, 0], {
          grid: [11, 6],
          from: "center",
          axis: "y"
        }),
        rotateZ: anime.stagger([0, 90, 0], {
          grid: [11, 6],
          from: "center",
          axis: "x"
        }),
        delay: anime.stagger(100, {
          grid: [11, 6],
          from: "center",
          start: 3000,
          easing: "easeOutQuad"
        })
      });
    }
  }
};
</script>

<style>
#entr {
  display: flex;
  flex-direction: column;
}
#path {
  height: 100px;
  margin: auto;
  background: #fff;
}
#path path {
  stroke: black;
}
</style>
